<template>
  <div id="welcome">
    <div class="welcome_con" v-show="!$store.state.isShow">
      <img src="../assets/img/play.png" class="cover" alt />
      <el-input
        placeholder="请输入歌手名或歌曲名"
        prefix-icon="el-icon-search"
        v-model="search"
        @change="searchSong"
        class="searchBox"
      ></el-input>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
    };
  },
  methods: {
    searchSong() {
      this.$store.commit("musicSearch", this.search);
      this.$store.commit("changShow");
    },
  },
};
</script>

<style lang="less">
#welcome {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to right, #085f94, #43677d);
  .searchBox {
    margin-top: 10px;
    box-shadow: 9px 13px 8px rgba(63, 63, 63, 0.6);
  }
  .welcome_con {
    width: 50%;
    margin-left: 189px;
  }
}
</style>